@import '../../variables';

.brand_container {
  display: flex;
  padding-top: 100px;
  max-height: 600px;
  justify-content: center;
  flex-direction: column;
  overflow: hidden;
  .brand_wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    margin: auto;
    padding: 100px 40px;
    padding-bottom: 30px;
    .brand {
      max-height: 150px;
      max-width: 150px;
      min-width: 60px;
      min-height: 60px;
      width: 100%;
      height: 100%;
      &:not(:last-child) {
        margin-right: 40px; } } } }

.news {
  max-width: 1600px;
  margin: auto;
  padding: 60px;
  &-cont {
    display: flex;
    justify-content: center;
    &-item {
      position: relative;
      width: 100%;
      &:first-child {
        margin-right: 10px; }
      > div {
        position: absolute;
        bottom: 0;
        color: #fff;
        padding: 50px; }
      img {
        width: 100%;
        height: 100%; } } } }

@media (max-width: 959.5px) {
  .news {
    padding: 0;
    &-cont {
      &-item:first-child {
        margin: 0; } } }
  .brand_wrapper {
    padding: 20px;
    padding-bottom: 0; } }

@media (max-width: 600px) {
  .brand_container {
    padding-top: 50px;
    .brand_wrapper {
      padding: 70px 40px;
      padding-bottom: 30px;
      .brand {
        &:not(:last-child) {
          margin-right: 20px; } } } }
  .news {
    padding: 20px 0px;
    &-cont {
      &-item {
        display: flex;
        justify-content: center;
        > div {
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          height: 100%;
          text-align: center;
          text-shadow: 0 0 5px #000;
          padding: 0 10px; } } } } }

@media (max-width: 600px) {
  h4 {
    font-size: 18px !important; }
  h2 {
    font-size: 24px !important; } }
