@import '../../../variables';

@mixin filterContainer($repeat) {
  display: grid;
  grid-gap: 4px;
  grid-template-columns: repeat($repeat, 1fr);
  width: 100%;
  padding: 20px 0;
  box-sizing: border-box; }

@mixin filterItems {
  box-sizing: border-box;
  user-select: none;
  cursor: pointer;
  transition: .4s; }

@mixin filterSize {
  background: #444;
  color: #fff; }

.Filter {
  display: flex;
  flex-direction: column;
  width: 260px;
  margin-right: 20px;
  background: #fff;
  box-sizing: border-box;
  transition: .4s;
  .Filter-reset {
    margin-top: 20px;
    margin-bottom: 40px; }
  &.hide {
    position: relative;
    transform: translateX(-260px);
    margin-left: -260px; }

  .FilterBlock {
    border-bottom: 1px solid #e5e5e5;
    margin-top: 20px;
    &-header {}
    &-content {
      @include filterContainer(4);

      .size {
        @include filterItems;
        padding: 8px 0;
        text-align: center;
        border: 1px solid #e5e5e5;
        border-radius: 4px;

        &.active, &:hover {
          @include filterSize; } }

      .color {
        @include filterItems;
        justify-self: center;
        width: 30px !important;
        height: 30px !important;
        margin: 5px 0;
        padding: 5px;
        border-radius: 50%;
        box-shadow: 0px 1px 2px rgba(0,0,0,0.2), 0px 2px 3px rgba(0,0,0,0.14), 0px 1px 5px rgba(0,0,0,0.12);
        &.active {
          svg {
            opacity: 1; } }
        svg {
          opacity: 0;
          color: #fff; }

        &.white {
          background: #fff;
          svg {
            color: #444; } }
        &.black {
          background: #000; }
        &.gray {
          background: #777; }
        &.dark-gray {
          background: #555; }
        &.brown {
          background: #421; }
        &.yellow {
          background: #ff0; }
        &.purple {
          background: #617; }
        &.green {
          background: #0c0; }
        &.red {
          background: #d00; }
        &.blue {
          background: #347; }
        &.pink {
          background: #ecb; }
        &.orange {
          background: #f80; } } } } }

@media (max-width: 959.5px) {
  .Filter {
    margin: 0;
    padding: 0 20px; } }
