@import '../../../variables';

.billingAddress {
  padding: 0 !important; }

.billing {
  border: 1px solid $light-gray;
  &-title {
    padding: 15px;
    box-sizing: border-box;
    font-size: 20px;
    font-weight: 500;
    color: $primary-black;
    background: $light-gray; }

  .billingAddressCheckbox {
    margin-top: 40px;
    margin-bottom: 40px; }

  form {
    padding: 20px;
    display: flex;
    flex-direction: column; } }
