@import '../../../variables';

.Carousel {
  max-width: 100%;
  &-block {
    display: flex !important;
    justify-content: center;
    padding: 10px;
    > div {
      position: relative;
      display: flex;
      justify-content: center;
      width: 100%;
      max-width: 1600px;
      padding: 0 20px;
      box-sizing: border-box; }
    img {
      width: 100%;
      height: 100%; }
    &-text {
      position: absolute;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      top: 0;
      left: 40px;
      padding: 60px;
      width: 100%;
      margin-right: -40px;
      box-sizing: border-box;
      color: $white;
      h2 {
        font-size: 60px;
        margin-bottom: 30px;
        text-shadow: 0px 0px 10px #000; }
      button {
        background: #f05;
        max-width: 100px;
        &:hover {
          background: rgba(#f05, .4); } } } } }

@media (max-width: 959.5px) {
  .Carousel {
    &-block {
      padding: 0;
      &-text {
        justify-content: flex-end;
        padding: 50px;
        bottom: 0;
        h2 {
          font-size: 40px; } } } } }

@media (max-width: 600px) {
  .Carousel {
    max-height: 840px;
    width: auto;
    .slick-list {
      overflow: hidden;
      max-height: 840px; }
    &-block {
      padding: 0;
      > div {
        padding: 0; }
      &-text {
        left: 0;
        padding: 20px;
        h2 {
          font-size: 40px;
          margin-bottom: 10px;
          text-shadow: 0px 0px 10px #000; } } } } }
